h1, h2, h3, h4, h5, h6 {
  margin: 1px;
}


.head {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: space-evenly;

}

button {
  box-shadow: #282c34;
  background-color: rgba(229, 215, 215, 0.708);
  color: rgb(32, 33, 34);
  justify-content: center;
  align-items: center;
  font-weight: bold;
  padding: 5px;
}

button:hover {
  background-color: #adc4ca;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
